// URL()
let good = 'api/goods';
let logins = 'api/auth/login';


// vmthisvmvuexuView
// https://uviewui.com/js/http.html#%E4%BD%95%E8%B0%93%E8%AF%B7%E6%B1%82%E6%8B%A6%E6%88%AA%EF%BC%9F
const install = (Vue, vm) => {
    // params
    //
    let goods= (params = {}) => vm.$u.get(good);

    // params
    //
    let loginInfo = (params = {}) => vm.$u.post(logins,params);

    // vm.$u.api(vmthisthis.$u.api)
    vm.$u.api = {goods, loginInfo};
}

export default {
    install
}